/*eslint-disable*/
import React, { useState, useEffect } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
// @material-ui/icons
import Email from "@material-ui/icons/Email";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import image from "assets/img/bg13_min.jpg";
import { useSelector } from "react-redux";
import FloatyAssistant from "components/Assistant/floaty";
import loginPageStyle from "assets/jss/material-kit-pro-react/views/loginPageStyle.js";
import { withCookies } from "react-cookie";
import doSaveAppVersion from "firebaseUtil/appVersion";
import productionVersion from "deployment/appversion";
import { appVersionRef } from "firebaseUtil/firebase";
import { firebaseAuth } from "firebaseUtil/firebase";
import * as paths from "routes/constUrl";

const SetAppVersionPage = ({ history }) => {

    const { ispassdoorman } = useSelector(state => state.login);

    useEffect(() => {
        window.scrollTo(0, 0);
        document.body.scrollTop = 0;
    });

    const classes = useStyles();
    const [appVersion, setAppVersion] = useState("");
    const [cloudAppVersion, setCloudAppVersion] = useState("");

    useEffect(() => {
        if (!ispassdoorman) {
            firebaseAuth.signOut()
                .then(() => {
                    //success
                })
                .catch(() => {
                    //error
                });

            history.replace(paths.DoormanPageUrl);
        }
    }, [])

    useEffect(() => {
        const maxMessageNumber = 5;

        if (appVersionRef) {
            appVersionRef.limitToLast(maxMessageNumber).on("value", snapshot => {
                const data = snapshot.val();
                if (data !== null && data !== undefined) {
                    // 最多顯示 5 則版本資訊
                    const sliceData = _.slice(_.values(data), -maxMessageNumber);

                    // 最新一則 Po 文的使用者
                    const currentVersion = _.slice(_.values(sliceData), -1)[0];

                    setCloudAppVersion(currentVersion);
                }
            });
        }
    }, [])

    const onSubmit = () => {

        if (appVersion && appVersion !== "") {
            doSaveAppVersion(appVersion);
            setAppVersion("");
        }

        event.preventDefault();
    }

    return (
        <div>
            <div
                className={classes.pageHeader}
                style={{
                    backgroundImage: "url(" + image + ")",
                    backgroundSize: "cover",
                    backgroundPosition: "top center"
                }}
            >
                <div className={classes.container}>
                    <GridContainer justify="center">
                        <GridItem xs={12} sm={12} md={4}>
                            <Card style={chatContainerBGColor}>
                                <form className={classes.form} onSubmit={onSubmit}>
                                    <p className={classes.description + " " + classes.textCenter} style={{ marginTop: "30px", color: "white", fontSize: "1.5em" }}>
                                        Cloud App Version: {cloudAppVersion}
                                    </p>
                                    <p className={classes.description + " " + classes.textCenter} style={{ marginTop: "30px", color: "white", fontSize: "1.5em" }}>
                                        Current Static File Version {productionVersion}
                                    </p>
                                    <CardBody signup>
                                        <CustomInput
                                            id="version"
                                            white
                                            formControlProps={{
                                                fullWidth: true
                                            }}
                                            inputProps={{
                                                placeholder: `Set Deploy Version`,
                                                type: "text",
                                                startAdornment: (
                                                    <InputAdornment position="start">
                                                        <Email className={classes.inputIconsColor} />
                                                    </InputAdornment>
                                                ),
                                                onChange: (e) => { setAppVersion(e.target.value) }
                                            }}
                                        />
                                    </CardBody>
                                    <div className={classes.textCenter}>
                                        <Button type="submit" simple color="info" size="lg">
                                            Get started
                                        </Button>
                                    </div>
                                </form>
                            </Card>
                        </GridItem>
                    </GridContainer>
                </div>
            </div>
            <FloatyAssistant></FloatyAssistant>
        </div>
    );
}

const useStyles = makeStyles(loginPageStyle);

// 變更預設登入畫面, 輸入帳密位置的背景顏色
const chatContainerBGColor = {
    backgroundColor: 'dimgray'
}

export default withCookies(SetAppVersionPage);
