import * as actionTypes from './action';

const initState = {
    maxMessageNumber: 70,
}

function reducer(state = initState, action) {
    switch (action.type) {
        case actionTypes.SET_MAX_MESSAGE_NUMBER:
            return {
                ...state,
                maxMessageNumber: action.payload
            };
        default:
            break;
    }

    return state;
}

export default reducer;